/** @format */

import { useAtom } from "jotai";
import { selectedDataType, selectedDistance } from "../SideHead";
import { centerLoc, selectedDataCount } from "../Map";
import React, { useState, useEffect } from "react";
import axios from "axios";
import "./Data.css";

//       10 
function formatCoordinate(value) {
  const numberValue = Number(value);
  return parseFloat(numberValue.toFixed(6));
}

const fetchScore = async (longitude, latitude, distance, setScore) => {
  const BASE_URL = "/api"; //  url 

  try {
    console.log("    :", {
      longitude,
      latitude,
      distance,
    });
    const response = await axios.get(`${BASE_URL}/map/safety-score`, {
      params: {
        longitude: longitude,
        latitude: latitude,
        distance: distance,
      },
    });
    // console.log("res:", response.data);
    const score = response.data["Safety_score"]; //  
    // console.log("score:", score);
    setScore(score);
  } catch (error) {
    console.log("    :", {
      longitude,
      latitude,
      distance,
    });
    console.error("Error fetching the score:", error);
    setScore(null);
  }
};

function getGradeDetails(score) {
  if (score >= 70) {
    return { color: "green", grade: "1", safety: "" };
  } else if (score >= 40) {
    return { color: "yellow", grade: "2", safety: "" };
  } else {
    return { color: "red", grade: "3", safety: "" };
  }
}

function GradeView() {
  const [score, setScore] = useState(null);
  const [distance] = useAtom(selectedDistance); // : 500m, 1000m (sidehead )
  const [centerLocation] = useAtom(centerLoc); //    (map.js ); 1-10   (?),  : { "latitude": 37.483869,"longitude": 127.084032 }
  const [dataType] = useAtom(selectedDataType);
  const [dataCount] = useAtom(selectedDataCount);

  useEffect(() => {
    if (dataCount > 0) {
      let { latitude, longitude } = centerLocation;
      longitude = formatCoordinate(longitude);
      latitude = formatCoordinate(latitude);

      fetchScore(longitude, latitude, distance, setScore);
    }
  }, [centerLocation, distance, dataCount]);

  if (score === null) {
    return <div>Loading...</div>;
  }

  const { color, grade, safety } = getGradeDetails(score);

  return (
    <div className="gradeContainer">
      <div data-tour="step-3" className="gradeImageContainer">
        <div className="gradeImage" id={color}>
          <p>{safety}</p>
          <p>
            {grade}/{score}
          </p>
        </div>
      </div>
      <div data-tour="step-4" className="gradeText">
        <p> {distance}M</p>
        <p>
          {dataType === "CCTV"
            ? `cctv ${dataCount || 0} `
            : dataType === ""
            ? ` ${dataCount || 0} `
            : "   "}
        </p>
      </div>
    </div>
  );
}

export default GradeView;
