/** @format */
import { atom, useAtom, useSetAtom } from "jotai";
import { selectedDataType, selectedDistance } from "./SideHead";
import styled from "styled-components";
import React, { useState, useEffect } from "react";
import {
	Map,
	MapMarker,
	MapTypeControl,
	ZoomControl,
	Circle,
} from "react-kakao-maps-sdk";
import axios from "axios";
import { selectedAddress } from "./Search/SearchBar";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useTour } from "@reactour/tour";

const { kakao } = window;

//      
const defaultLoc = atom({
	latitude: 37.556809,
	longitude: 126.971566,
});
//         
export const centerLoc = atom((get) => get(defaultLoc));

const dataCnt = atom(0);
export const selectedDataCount = atom((get) => get(dataCnt));

//       10 
function formatCoordinate(value) {
	const numberValue = Number(value);
	return parseFloat(numberValue.toFixed(6));
}

// cctv   
// http://kdt-ai-10-team01.elicecoding.com
// http://kdt-ai-10-team01.elicecoding.com:3000/map/cctv?longitude=127.084032&latitude=37.483869&distance=1000
async function fetchCCTVData(longitude, latitude, distance) {
	const BASE_URL = "/api"; //  URL 

	const baseInstance = await axios.create({
		baseURL: BASE_URL, //  URL 
	});

	// 1-10 limit ( )
	try {
		const response = await baseInstance.get(`/map/cctv`, {
			params: {
				longitude: longitude,
				latitude: latitude,
				distance: distance,
			},
		});
		const cctvData = response.data;
		// console.log("cctv data:", cctvData);
		return cctvData;
	} catch (error) {
		console.log("  :", { longitude, latitude, distance });
		console.log("cctv   :", error);
	}
}

//    
async function fetchSecurityLight(longitude, latitude, distance) {
	const BASE_URL = "/api"; //  URL 

	const baseInstance = await axios.create({
		baseURL: BASE_URL, //  URL 
	});

	// 1-10 limit ( )
	try {
		const response = await baseInstance.get(`/map/security-light`, {
			params: {
				longitude: longitude,
				latitude: latitude,
				distance: distance,
			},
		});
		const securityLight = response.data;
		return securityLight;
	} catch (error) {
		console.log("  :", { longitude, latitude, distance });
		console.log("   :", error);
	}
}

const CircleButton = styled.button`
	position: absolute;
	top: 8px;
	right: 125px;
	z-index: 1000;
	display: inline-block;
	width: 50px;
	height: 50px;
	font-size: 21px;
	font-weight: 900;
	border-radius: 50%;
	background-color: #004aad;
	opacity: 80%;
	border: none;
	color: white; /*  */
	text-align: center;
	line-height: 50px; /*       */
	cursor: pointer; /*     */

	/*     */
	&:hover {
		width: 120px;
		opacity: 100%;
		font-size: 15px;
		font-weight: 400;
		border-radius: 20px;
	}

	@media (max-width: 786px) {
		top: 21vh;
		left: 1vh;
		width: 40px;
		height: 40px;
		line-height: 40px;
	}
`;

//   
export default function KakaoMap() {
	const { setIsOpen } = useTour();
	const [address] = useAtom(selectedAddress);
	const [centerLocation, setCenterLocation] = useAtom(defaultLoc); //  
	const [locations, setLocations] = useState([]); //   json list
	const [dataType] = useAtom(selectedDataType); //    : cctv, 
	const [distance] = useAtom(selectedDistance); // : 500m, 1000m
	const [count, setCount] = useAtom(dataCnt); //    
	const [hovered, setHovered] = useState(false);

	useEffect(() => {
		console.log(
			`  ${address}  ${distance}m   ${dataType}   !`
		);
	}, [address, dataType, distance]);

	useEffect(() => {
		if (address) {
			//        ( )
			let geocoder = new kakao.maps.services.Geocoder();
			geocoder.addressSearch(address, (result, status) => {
				if (status === kakao.maps.services.Status.OK) {
					// console.log("converted addr:", result[0]);
					let { x: longitude, y: latitude } = result[0];
					// longitude = formatCoordinate(longitude);
					// latitude = formatCoordinate(latitude);
					setCenterLocation({ latitude, longitude });
				} else {
					console.error("Invalid Address: " + status);
				}
			});
		}
	}, [address]); //            

	useEffect(() => {
		const latitude = formatCoordinate(centerLocation.latitude);
		const longitude = formatCoordinate(centerLocation.longitude);

		if (dataType === "CCTV") {
			console.log("CCTV ");

			fetchCCTVData(longitude, latitude, distance)
				.then((data) => {
					setLocations(data);
					setCount(data.length);
					console.log("fetched cctv data:", data);
					console.log("cctv :", data.length);
				})
				.catch((error) => {
					toast.info(
						` cctv .
            (     .)`
					);
					setCount(0);
				});
		} else if (dataType === "") {
			console.log(" ");
			console.log(`: ${distance}m`);
			console.log(":", address);

			fetchSecurityLight(longitude, latitude, distance)
				.then((data) => {
					setLocations(data);
					setCount(data.length);
					console.log("fetched  data:", data);
					console.log(" :", data.length);
				})
				.catch((error) => {
					toast.info(
						"  .\n(, , ,    .)"
					);
					setCount(0);
				});
		}
	}, [centerLocation, dataType, distance]); //   ,  ,        

	//        
	const handleDragEnd = (marker) => {
		const lat = marker.getPosition().getLat();
		const lng = marker.getPosition().getLng();
		setCenterLocation({ latitude: lat, longitude: lng });
	};

	return (
		<>
			<CircleButton
				className="circle-button"
				onClick={() => setIsOpen(true)}
				onMouseEnter={() => setHovered(true)}
				onMouseLeave={() => setHovered(false)}
			>
				{hovered ? "?" : "?"}
			</CircleButton>
			{centerLocation && (
				<Map
					center={{
						lat: centerLocation.latitude,
						lng: centerLocation.longitude,
					}}
					style={{ width: "100%", height: "100vh" }}
					level={3}
				>
					<MapMarker
						position={{
							lat: centerLocation.latitude,
							lng: centerLocation.longitude,
						}}
						draggable={true}
						onDragEnd={handleDragEnd}
					/>
					<Circle
						center={{
							lat: centerLocation.latitude,
							lng: centerLocation.longitude,
						}}
						radius={distance}
						strokeWeight={5} //  
						strokeColor={"#75B8FA"} //  
						strokeOpacity={1} //   1 0   0  
						strokeStyle={"dashed"} //  
						fillColor={"#CFE7FF"} // 
						fillOpacity={0.3} //  
					/>
					{locations &&
						locations.map((loc, idx) => (
							<MapMarker
								key={`${loc.latitude}-${idx}`}
								position={{
									lat: loc.latitude,
									lng: loc.longitude,
								}}
								image={{
									src: "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png",
									size: { width: 24, height: 35 },
								}}
							/>
						))}
					<MapTypeControl position={"TOPRIGHT"} />
					<ZoomControl position={"RIGHT"} />
				</Map>
			)}
		</>
	);
}
